// DC.com  - SetIntro.js
import React from "react";

// 
import { secIntroData } from "../data/sec_intro";
import "../../css/sec_intro.scss";
import { Link } from "react-router-dom";

function SetIntro(props) {
  //   
  const selData = secIntroData;
  return (
    <>
      <section className="sec-intro">
        {/*  */}
        {selData.map((v, i) => (
          <div key={i}>
            {/*  */}
            <div className="imbx">
              <img src={process.env.PUBLIC_URL+v.isrc} alt={v.tit.split("^")[0]} />
              {/* split()   */}
            </div>
            {/*  */}
            <div className="titbx">
              <h3>{v.tit.split("^")[0]}</h3>
              <h2>{v.tit.split("^")[1]}</h2>
            </div>
            {/*  */}
            <div className="btnbx">
              <button>
                <Link to={v.link}>{v.btn}</Link>
              </button>
            </div>
          </div>
        ))}
      </section>
    </>
  );
}

export default SetIntro;
