import React, { useEffect, useState } from "react";
import DaumPostcode from "react-daum-postcode";

const AddressInput = ({changeAddr}) => {
    // changeAddr -   

    //  ///////////////
    // [1] 
    const [zonecode, setZonecode] = useState("");
    // [2] 
    const [address, setAddress] = useState("");
    // [3]   
    const [isOpen, setIsOpen] = useState(false);

    //  /////////////////
    // [1]     ( API)
    const themeObj = {
        bgColor: "#FFFFFF",
        pageBgColor: "#FFFFFF",
        postcodeTextColor: "#C05850",
        emphTextColor: "#222222",
    };

    // [2]   
    const postCodeStyle = {
        width: "40vw",
        height: "60vh",
    };

    // [3]   
    const wholeBoxStyle = {
        display: "inline-block",
        verticalAlign: "top",
    };

    // [4]    
    const popupWindowStyle = {
        position: "fixed",
        top: "50%",
        left: "50%",
        translate: "-50% -50%",
        backgroundColor: "white",
        padding: "20px",
        border: "4px double #000",
        zIndex: "1",
    };

    // [5]  
    const closeButtonStyle = {
        position: "absolute",
        top: "0",
        right: "0",
        fontSize: "40px",
        backgroundColor: "transparent",
        border: "none",
    };

    //   //////////////////
    // [1]  
    const completeHandler = (data) => {
        const { address, zonecode } = data;
        setZonecode(zonecode);
        setAddress(address);
    };

    // [2]  
    const closeHandler = (state) => {
        // 
        if (state === "FORCE_CLOSE") {
            setIsOpen(false);
        } 
        //  
        else if (state === "COMPLETE_CLOSE") {
            setIsOpen(false);
        }
    };

    // [3]  /  
    const toggleHandler = () => {
        setIsOpen((prevOpenState) => !prevOpenState);
    };

    //   :  ////////////
    useEffect(()=>{
        //    
        changeAddr(); // ->    !
    }); ////// useEffect //////////


    //  //////////////////////
    return (
        <div style={wholeBoxStyle}>
            <div>
                <div style={{ paddingLeft: "20px", lineHeight: "2" }}>
                    <button type="button" onClick={toggleHandler}>
                        Search Address
                    </button>
                    {/*   */}
                    <div>
                        ZipCode :
                        <input className="zipcode" 
                        value={zonecode} readOnly
                        onClick={toggleHandler}
                        placeholder="Click 'Search Address'" />
                    </div>
                </div>
                {isOpen && (
                    <div style={popupWindowStyle}>
                        <DaumPostcode theme={themeObj} style={postCodeStyle} onComplete={completeHandler} onClose={closeHandler} />
                        <button style={closeButtonStyle} onClick={toggleHandler}>
                            
                        </button>
                    </div>
                )}
                {/*   */}
                <input className="addr1" value={address} readOnly 
                onClick={toggleHandler}
                style={{ width: "100%" }} 
                placeholder="Click 'Search Address'" />
                {/*   () */}
                <input className="addr2" placeholder="input detail adress" style={{ width: "100%" }}
                onChange={changeAddr}
                onBlur={changeAddr}
                />
            </div>
        </div>
    );
};

export default AddressInput;
