//    ///

import { useCallback, useEffect, useState } from "react";
import { FooterArea } from "./FooterArea";
import MainArea from "./MainArea";
import { TopArea } from "./TopArea";

//  API 
import { dCon } from "../modules/dCon";
import { useNavigate } from "react-router-dom";

import $ from "jquery";
// import "jquery.cookie";

export default function Layout() {
  // [   ] //////////////
  // 1.  
  const [loginSts, setLoginSts] = useState(sessionStorage.getItem("minfo"));

  //     !
  // const  = useCallback((x)=>{setLoginSts(x)},[loginSts])

  // ->   "minfo" 

  // 2.    
  const [loginMsg, setLoginMsg] = useState(null);
  // console.log(loginMsg);

  // [   ] ///
  // 1.   :   useNavigate
  //  useCallback()    !
  const goNav = useNavigate();
  //      !
  //   useCallback() !
  const goPage = useCallback((pm1, pm2) => {
    goNav(pm1, pm2);
  }, []);

  // 2.   
  const makeMsg = useCallback((name) => {
    // 
    let usrIcon = ["", "", "", "", ""];
    //  : 0~4 
    let rdm = Math.floor(Math.random() * 5);
    //    
    setLoginMsg(`Welcome ${name} ${usrIcon[rdm]}`);
  }, []); /////// makeMsg  /////////

  // 3.   /////////
  const logoutFn = useCallback(() => {
    // 1.   null
    setLoginSts(null);
    // 2.   : minfo
    sessionStorage.removeItem("minfo");
    // 3.   
    setLoginMsg(null);
    // 4.   
    goPage("/");
  }, []); //////// logoutFn  /////////

  //    ////////
  useEffect(() => {
    // ->    //////
    //  (minfo)  null 
    //    !
    // -> null   true!
    if (sessionStorage.getItem("minfo")) {
      //  
      let ss = sessionStorage.getItem("minfo");
      //  
      setLoginSts(ss);
      //    :
      // ->  unm() !
      makeMsg(JSON.parse(ss).unm);
    } ///// if ///////
    // $.cookie("aa", "bb", { expires: 2 });
  }, []);

  ////   //////////////
  return (
    // Provider value   !
    <dCon.Provider
      value={{
        loginSts,
        setLoginSts,
        loginMsg,
        setLoginMsg,
        goPage,
        makeMsg,
        logoutFn,
      }}
    >
      {/* 1. :   ! */}
      <TopArea
        loginMsg={loginMsg}
        loginSts={loginSts}
        logoutFn={logoutFn}
        goPage={goPage}
      />
      {/* 2. */}
      <MainArea />
      {/* 3. */}
      <FooterArea />
    </dCon.Provider>
  );
} /////////// Layout /////////////////////
