import React, { useRef, useState } from "react";

// 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// CSS
import "../../css/searching.scss";

//   :   
import { catListData } from "../data/swiper_cat";


//    
import SearchingCat from "./SearchingCat";

function Searching({kword}) {
    // kword -  
    console.log("kword:", kword);
    console.log("data:", catListData);

    //    
    //     !!!
    // (()) //////////////
    // [1]  
    const [kw,setKw] = useState(kword);
    //     !
    // [2]  
    const [sort,setSort] = useState("asc");
    // :  - asc /  - desc
    // [3]   
    const [chk,setChk] = useState([true,true,true]);
    //      
    console.log(":",chk);

    //     
    //       !
    //     ? useRef !
    const beforeKword = useRef(kword);
    //  !  
    // current    !
    console.log(":",beforeKword);
    
    //      
    //     !
    if(beforeKword.current != kword){
      console.log(beforeKword.current,"==?",kword);
      // 1.   ()
      setKw(kword);
      // 2.       
      beforeKword.current = kword;
      // 3.     
      document.querySelector("#schin").value=kword;

    } /////// if //////////


    //    
    // filter()    !
    const newList = catListData.filter(v=>{
        //     (v.cname)
        //     
        let newVal = v.cname.toLocaleLowerCase();
        //   
        // ((!!!))  kw !!!
        let key = kw.toLocaleLowerCase();
        //     !
        if(
          // 1 2   true!
          // 1.  (cname)
          (newVal.indexOf(key) !== -1) &&
          // 2.   (alignment)
          // :     
          //  (&&,||,!)  !
          // OR  false  false!
          //    false !
          (
           (chk[0]?v.alignment=="hero":false) ||
           (chk[1]?v.alignment=="comp":false) ||
           (chk[2]?v.alignment=="villain":false)
          )
          //true && (true||false||false)
          // -> &&  true true
          // -> ||  true true
          
          ) return true;
        // .indexOf()  
        //    -1 !
        //  -1  true 
        // filter    !
    }); //////////////// filter ///////////////////

    // [   :   alignment ! ]

    // [   ] /////////
    // (1)  
    if(sort == "asc"){
      newList.sort((a,b)=>
        a.cname > b.cname 
        ? 1 : a.cname < b.cname 
        ? -1 : 0
      );

    } /// if ///////////////////////
    // (2)  
    else if(sort == "desc"){
      newList.sort((a,b)=>
      a.cname > b.cname 
      ? -1 : a.cname < b.cname 
      ? 1 : 0
    );
    } /// else if ///////////////////



    console.log("newList:", newList);
    /* 
         = .filter(v=>{
            if(v..indexOf()!=-1) return true
        })

        ->       !
        ->  ,   !
    */

  //   ////////////////////////
  return (
    <>
      {/*    */}
      <section className="schbx">
        {/* 1.  */}
        <div className="schopt">
          {/* 1-1. */}
          <div className="searching">
            {/*    */}
            <FontAwesomeIcon
              icon={faSearch}
              className="schbtn"
              title="Open search"
            />
            {/*  */}
            <input 
            id="schin" 
            type="text" 
            placeholder="Filter by Keyword"
            defaultValue={kword} 
            //   !
            //    !!!
            // -> setKw()
            onKeyUp={(e)=>{
              if(e.key=="Enter"){ 
                // 1.   
                setKw(e.target.value);
                // 2.    (asc)
                setSort("asc");
                // 3.  
                setChk([true,true,true]);
                //  (DOM )
                document.querySelector("#sel").value = "asc";
              } /// if ///
            }}
            />
          </div>
          {/* 1-2.  */}
          <div className="chkbx">
            <ul>
              <li>
                {/*  */}
                <h2>
                  ALIGNMENT
                  <span className="spbtn"></span>
                </h2>
                {/*  */}
                <ol>
                  <li>
                    Heroes
                    {/*   */}
                    <input 
                    type="checkbox" 
                    id="hero" 
                    className="chkhdn"
                    //   !
                    checked={chk[0]}
                    //  change
                    onChange={(e)=>{
                      //  checked
                      //  true,  false
                      console.log(e.target.checked);
                      //  
                      setChk([
                        e.target.checked,
                        chk[1],
                        chk[2]
                      ]);
                    }}

                    />
                    {/*   */}
                    <label htmlFor="hero" className="chklb"></label>
                  </li>
                  <li>
                    It's Complicated
                    {/*   */}
                    <input 
                    type="checkbox" 
                    id="comp" 
                    className="chkhdn"                     
                    //   !
                    checked={chk[1]}
                    //  change
                    onChange={(e)=>{
                      //  checked
                      //  true,  false
                      console.log(e.target.checked);
                      //  
                      setChk([
                        chk[0],
                        e.target.checked,
                        chk[2]
                      ]);
                    }}
                    />
                    {/*   */}
                    <label htmlFor="comp" className="chklb"></label>
                  </li>
                  <li>
                    Villains
                    {/*   */}
                    <input 
                    type="checkbox" 
                    id="villain" 
                    className="chkhdn"                     
                    //   !
                    checked={chk[2]}
                    //  change
                    onChange={(e)=>{
                      //  checked
                      //  true,  false
                      console.log(e.target.checked);
                      //  
                      setChk([
                        chk[0],
                        chk[1],
                        e.target.checked,
                      ]);
                    }}
                    />
                    {/*   */}
                    <label htmlFor="villain" className="chklb"></label>
                  </li>
                </ol>
              </li>
            </ul>
          </div>
        </div>
        {/* 2.  */}
        <div className="listbx">
          {/* 2-1.   */}
          <h2 className="restit">BROWSE CHARACTERS({newList.length})</h2>
          {/* 2-2.  */}
          <aside className="sortbx">
            <select 
            name="sel" 
            id="sel" 
            className="sel"
            //   
            onChange={(e)=>{
              console.log(e.target.value);
              //   
              setSort(e.target.value);
            }}
            >
              <option value="asc">A-Z</option>
              <option value="desc">Z-A</option>
            </select>
          </aside>
          {/* 2-3.    : 
                 */}
            <SearchingCat dt={newList} />
        </div>
      </section>
    </>
  );
}

export default Searching;
