//    ///
import { Fragment, useContext, useRef, useState } from "react";

//    
import { initData } from "../func/mem_fn";

//     ->  !!!
// import baseData from "../data/board.json";
//      !
//       !

// 
import $ from "jquery";

//  CSS
import "../../css/board.scss";
import "../../css/board_file.scss";

//   JS
import { initBoardData } from "../func/board_fn";
import { dCon } from "../modules/dCon";

export default function Board() {
  //  
  const myCon = useContext(dCon);
  //     (!)
  const sts = myCon.loginSts;
  // console.log(":", sts);

  //    ! //
  initBoardData();

  //   !
  const baseData = JSON.parse(localStorage.getItem("board-data"));

  // [   ] ///
  // [1]  
  const [pageNum, setPageNum] = useState(1);
  // [2] 
  const [mode, setMode] = useState("L");
  // (1)  (L) : List Mode
  // (2)  (R) : Read Mode
  // (3)  (W) : Write Mode
  // (4)  (M) : Modify Mode ()

  // [  ] ///
  // [1]   -    !
  const totalCount = useRef(baseData.length);
  // console.log(":", totalCount);
  // [2]   
  const selRecord = useRef(null);
  // ->      !

  //  
  const unitSize = 8;

  /********************************************** 
        : bindList
         :    
  **********************************************/
  const bindList = () => {
    // console.log(baseData);

    // 1.   
    let orgData = baseData;

    // 2.   : 
    orgData.sort((a, b) =>
      Number(a.idx) > Number(b.idx) ? -1 : Number(a.idx) < Number(b.idx) ? 1 : 0
    );

    // 3.   
    //  0 9 
    //  10...
    //    !
    // 1,2,3,4,...

    //  = (-1)*
    let sNum = (pageNum - 1) * unitSize;
    //  = *
    let eNum = pageNum * unitSize;
    // console.log(":", sNum, "/:", eNum);
    // 
    const selData = [];

    // for  
    for (let i = sNum; i < eNum; i++) {
      // console.log(i);
      //     !
      if (i >= totalCount.current) break;
      //  
      selData.push(orgData[i]);
    } ///// for //////

    // console.log(":", selData);

    return selData.map((v, i) => (
      <tr key={i}>
        {/*      */}
        <td>{i + 1 + sNum}</td>
        <td>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              //  !
              setMode("R");
              //   
              selRecord.current = v;
            }}
          >
            {v.tit}
          </a>
        </td>
        <td>{v.unm}</td>
        <td>{v.date}</td>
        <td>{v.cnt}</td>
      </tr>
    ));
  }; /////////// bindList  /////////////////

  /****************************************** 
     : pagingList
     :     
  ******************************************/
  const pagingList = () => {
    //    :  / 
    // :     +1

    // 1.  
    let pagingCount = Math.floor(totalCount.current / unitSize);

    //     !
    //  0  1
    if (totalCount.current % unitSize > 0) {
      pagingCount++;
    }

    // console.log(
    //   ":",
    //   pagingCount,
    //   ":",
    //   totalCount.current % unitSize
    // );

    //   ///
    const pgCode = [];

    // 1    
    for (let i = 1; i <= pagingCount; i++) {
      pgCode.push(
        <Fragment key={i}>
          {
            //    b
            i === pageNum ? (
              <b>{i}</b>
            ) : (
              //   
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setPageNum(i);
                }}
              >
                {i}
              </a>
            )
          }
          {/*   */}
          {i !== pagingCount && " | "}
        </Fragment>
      );
    } ////// for /////

    // 
    return pgCode;
  }; ////////// pagingList  //////////////

  //    ////////
  const clickButton = (e) => {
    //  
    let btnText = e.target.innerText;
    // console.log(btnText);
    //  
    switch (btnText) {
      //   
      case "Write":
        setMode("W");
        break;
      //  
      case "List":
        setMode("L");
        break;
      //   !
      case "Submit":
        submitFn();
        break;
      //    
      case "Modify":
        setMode("M");
        break;
    }
  }; ////////// clickButton //////////

  //   //////////////
  const submitFn = () => {
    // 
    let title = $(".subject").val().trim();
    // 
    let cont = $(".content").val().trim();
    // trim()   !

    // 1.  
    // ,   !
    if (title == "" || cont == "") {
      alert("Insert title or content!");
      return; //  !
    } ////// if ////

    // 2.   (mode=="W")
    if (mode == "W") {
      // 0.    
      let person = JSON.parse(sts);

      // 1.   /////
      let today = new Date();
      // yy-mm-dd  
      //   : toJSON()
      // ISO  : toISOString()
      //    10 !
      // .substr(0,10)

      // 2.   /////
      //   idx   
      let arrIdx = baseData.map((v) => parseInt(v.idx));
      // console.log(arrIdx);
      //   :    !
      let maxNum = Math.max(...arrIdx);
      // console.log(maxNum);

      // 3.     ////
      let data = {
        idx: maxNum + 1,
        tit: title,
        cont: cont,
        att: "",
        date: today.toJSON().substr(0, 10),
        uid: person.uid,
        unm: person.unm,
        cnt: "0",
      };
      // console.log(" :",data);

      // 4.   //////
      // (1)  
      let locals = localStorage.getItem("board-data");
      locals = JSON.parse(locals);
      // (2)  push
      locals.push(data);
      // (3)    
      localStorage.setItem("board-data", JSON.stringify(locals));

      //  !
      // console.log(localStorage.getItem("board-data"));

      // 5.   /////
      // -> ! "L"
      setMode("L");
    } /// if ///
    // 3.   (mode=="M")
  }; ////////// submitFn //////////////

  ////   //////////////
  return (
    <main className="cont">
      <h1 className="tit">OPINION</h1>
      {
        // 1.     
        mode == "L" && <ListMode bindList={bindList} pagingList={pagingList} />
      }
      {
        // 2.     
        mode == "R" && <ReadMode selRecord={selRecord} />
      }
      {
        // 3.      
        // sts    
        mode == "W" && <WriteMode sts={JSON.parse(sts)} />
      }
      {
        // 4.     
        mode == "M" && <ModifyMode selRecord={selRecord} />
      }
      <br />
      {/*    */}
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td>
              {
                // 1.    "L"
                mode == "L" && sts && (
                  <button onClick={clickButton}>Write</button>
                )
              }
              {
                // 2.  "R"  
                <>
                {mode == "R" && 
                <button onClick={clickButton}>List</button>}

                {
                  // console.log(":",
                  // JSON.parse(sts).uid,
                  // "==?",
                  // selRecord.current.uid)
                }
                
                { //    
                //   !
                //  selRecord  
                //   uid   !
                //   sts.uid 
                (mode == "R" && sts && 
                JSON.parse(sts).uid==selRecord.current.uid) && 
                <button onClick={clickButton}>Modify</button>
                }
                </>
              }
              {
                // 3.  "W"  
                mode == "W" && (
                  <>
                    <button onClick={clickButton}>Submit</button>
                    <button onClick={clickButton}>List</button>
                  </>
                )
              }
              {
                // 4.  "M"  
                mode == "M" && (
                  <>
                    <button onClick={clickButton}>Submit</button>
                    <button onClick={clickButton}>Delete</button>
                    <button onClick={clickButton}>List</button>
                  </>
                )
              }
            </td>
          </tr>
        </tbody>
      </table>
    </main>
  );
} /////////// Board /////////////////////

/****************************************** 
           
******************************************/
const ListMode = ({ bindList, pagingList }) => {
  return (
    <>
      <div className="selbx">
        <select name="cta" id="cta" className="cta">
          <option value="tit">Title</option>
          <option value="cont">Contents</option>
          <option value="unm">Writer</option>
        </select>
        <select name="sel" id="sel" className="sel">
          <option value="0">Descending</option>
          <option value="1">Ascending</option>
        </select>
        <input id="stxt" type="text" maxLength="50" />
        <button className="sbtn">Search</button>
      </div>
      <table className="dtbl" id="board">
        <thead>
          <tr>
            <th>Number</th>
            <th>Title</th>
            <th>Writer</th>
            <th>Date</th>
            <th>Hits</th>
          </tr>
        </thead>
        <tbody>{bindList()}</tbody>
        <tfoot>
          <tr>
            <td colSpan="5" className="paging">
              {pagingList()}
            </td>
          </tr>
        </tfoot>
      </table>
    </>
  );
}; //////////// ListMode ///////////////////

/****************************************** 
           
******************************************/
const ReadMode = ({ selRecord }) => {
  //    
  //     !
  //      !
  // console.log(" :", selRecord.current);
  //     
  const data = selRecord.current;

  return (
    <>
      <table className="dtblview readone">
        <caption>OPINION : Read</caption>
        <tbody>
          <tr>
            <td>Name</td>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly
                value={data.unm}
              />
            </td>
          </tr>
          <tr>
            <td>Title</td>
            <td>
              <input
                type="text"
                className="subject"
                size="60"
                readOnly
                value={data.tit}
              />
            </td>
          </tr>
          <tr>
            <td>Content</td>
            <td>
              <textarea
                className="content"
                cols="60"
                rows="10"
                readOnly
                value={data.cont}
              ></textarea>
            </td>
          </tr>
          <tr>
            <td>Attachment</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </>
  );
}; ///////////// ReadMode //////////////////

/****************************************** 
           
******************************************/
const WriteMode = ({ sts }) => {
  // sts -  
  //    !
  // console.log(sts);

  return (
    <>
      <table className="dtblview readone">
        <caption>OPINION : Write</caption>
        <tbody>
          <tr>
            <td>Name</td>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly
                //  
                value={sts.unm}
              />
            </td>
          </tr>
          <tr>
            <td>Email</td>
            <td>
              <input
                type="text"
                className="email"
                size="40"
                readOnly
                //  
                value={sts.eml}
              />
            </td>
          </tr>
          <tr>
            <td>Title</td>
            <td>
              <input type="text" className="subject" size="60" />
            </td>
          </tr>
          <tr>
            <td>Content</td>
            <td>
              <textarea className="content" cols="60" rows="10"></textarea>
            </td>
          </tr>
          <tr>
            <td>Attachment</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </>
  );
}; ///////////// WriteMode //////////////////

/****************************************** 
           
******************************************/
const ModifyMode = ({ selRecord }) => {
  //    
  //     !
  //      !
  // console.log(" :", selRecord.current);
  //     
  const data = selRecord.current;

  return (
    <>
      <table className="dtblview readone">
        <caption>OPINION : Modify</caption>
        <tbody>
          <tr>
            <td>Name</td>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly
                value={data.unm}
              />
            </td>
          </tr>
          <tr>
            <td>Title</td>
            <td>
              <input
                type="text"
                className="subject"
                size="60"
                defaultValue={data.tit}
              />
            </td>
          </tr>
          <tr>
            <td>Content</td>
            <td>
              <textarea
                className="content"
                cols="60"
                rows="10"
                defaultValue={data.cont}
              ></textarea>
            </td>
          </tr>
          <tr>
            <td>Attachment</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </>
  );
}; ///////////// ModifyMode //////////////////
