// DC.com  
import React from "react";

//    
import { isrc } from "../data/img_src";

export default function Logo({ logoStyle }) {
  // logoStyle : ,    

  //   
  const myStyle = {
    top: {
      width: "45px",
      height: "45px",
      marginRight: "30px",
      borderRadius: "50%",
      cursor: "pointer",
    },

    bottom: {
      height: "80px",
    },
  };

  //   
  const imgStyle = {
    top: {width:"45px"},
    bottom: {width:"80px"},
  };
  return (
    <h1 style={myStyle[logoStyle]}>
      <img style={imgStyle[logoStyle]} src={process.env.PUBLIC_URL+isrc.logo} alt="DC" />
    </h1>
  );
} ///////////Logo.////////////////
