//       

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

//   
// ->      .
// -> ,   ->>>   ,    .

// [   ]
// 1)     
// 2)   new 
// 3)  ,  this 
function SlideFn() {
  // 1.  //////////////////
  // (1) 
  const A_TM = 600;
  // (2) 
  const A_ES = "easeInOutQuint";
  // (3) (1-,0-)
  let cSts = 0;
  // (4) 
  let sNum = 0;

  //// 2.    ///////
  ////       !!
  //->        !

  // this.=  <<<   this       .
  
  this.goSlide = (e) => {
    // 1.   
    const tg = e.target;
    // console.log(tg);

    // 2. 
    // (1)  :    !
    const sldBox = $(tg).siblings(".slider");
    // (2)   :  .indic!
    const indic = $(tg).siblings(".indic").find("li");
    // console.log(':',indic);
    // (3)  
    const sCnt = sldBox.find("li").length;
    // console.log(':',sCnt);

    // 3. 
    // 0.  /////////////
    if (cSts) return;
    cSts = 1; //
    setTimeout(() => (cSts = 0), A_TM);
    ////////////////////////////

    // 1.  
    let isR = $(tg).is(".rb");
    console.log("!", isR);

    // 2. 
    // 2-1. 
    if (isR) {
      //   
      // left -100%
      sldBox.animate({ left: "-100%" }, A_TM, A_ES, () => {
        // ()
        // li  
        sldBox
          .append(sldBox.find("li").first())
          //  left 0 !
          .css({ left: "0" });
      });
      //   (  0)
      sNum++;
      if (sNum >= sCnt) sNum = 0;
    } //////// if /////////
    // 2-2. 
    else {
      // li  
      sldBox
        .prepend(sldBox.find("li").last())
        // left -100%
        .css({ left: "-100%" })
        // left 0 
        .animate({ left: "0" }, A_TM, A_ES);

      //  (0  )
      sNum--;
      if (sNum < 0) sNum = sCnt - 1;
    } /////// else /////////

    console.log(":", sNum);

    //  'on'(li )
    indic.eq(sNum).addClass("on").siblings().removeClass("on");
  }; ////////////// goSlide /////////////
} //////////////SlideFn

export default SlideFn;
