//    - Login.jsx
import React, { useContext, useEffect, useState } from "react";

// CSS  ( )
import "../../css/member.scss";

//     !
import { initData } from "../func/mem_fn";
import { dCon } from "../modules/dCon";

function Login() {
  //  
  const myCon = useContext(dCon);
  // console.log(myCon.loginSts);

  // [  ] /////////////
  // [1]  
  // 1. 
  const [userId, setUserId] = useState("");
  // 2. 
  const [pwd, setPwd] = useState("");

  // [2]  
  // ->   (false)
  // 1. 
  const [userIdError, setUserIdError] = useState(false);
  // 2. 
  const [pwdError, setPwdError] = useState(false);

  // [    ] ////
  const msgId = [
    "This is a required entry", //
    "ID does not exist", //  
  ];
  // [    ] ////
  const msgPwd = [
    // 
    "This is a required entry", //
    "Password doesn't match", //  
  ];

  // [3]   :  msgId[0]
  // ->   
  const [idMsg, setIdMsg] = useState(msgId[0]);
  const [pwdMsg, setPwdMsg] = useState(msgPwd[0]);

  // [    ] ///////
  // 1.    ////////////
  const changeUserId = (e) => {
    //  
    let val = e.target.value;

    // 1.   :
    // 1-1. (false)
    if (val !== "") setUserIdError(false);
    // 1-2. (true)
    else {
      // (1)  ()
      setIdMsg(msgId[0]);
      // (2)  
      setUserIdError(true);
    } /////// else ///////////

    //  userId   
    //  !
    setUserId(val);
  }; ////////// changeUserId  ////////////

  // 2.    ///////////
  const changePwd = (e) => {
    //  
    let val = e.target.value;

    // 1.   :
    // 1-1. (false)
    if (val !== "") setPwdError(false);
    // 1-2. (true)
    else {
      // (1)  ()
      setPwdMsg(msgPwd[0]);
      // (2)  
      setPwdError(true);
    } /////// else ///////////

    // 4.  
    setPwd(val);
  }; ///////// changePwd  //////////

  // [    ] ///////////
  const totalValid = () => {
    // 1.     !
    if (!userId) setUserIdError(true);
    if (!pwd) setPwdError(true);

    // 2.  true,  false 
    //  :  +   false
    if (userId && pwd && !userIdError && !pwdError) return true;
    //  false false !
    else return false;
  }; /////////// totalValid  ///////////

  // [   ] ////////////////
  const onSubmit = (e) => {
    // 1.  
    e.preventDefault();

    console.log(":", totalValid());

    // 2.   
    if (totalValid()) {
      console.log("! !");

      // [  ]

      // 1.  ( !)
      initData();

      // 2.  
      let memData = localStorage.getItem("mem-data");

      // 3.  
      memData = JSON.parse(memData);
      console.log(memData);

      // 4.    
      let result = memData.find((v) => {
        if (v.uid === userId) return true;
      }); /////// find ///////
      console.log(":", result);

      // 4-1.    
      if (!result) {
        // (1)  
        setIdMsg(msgId[1]);

        // (2)  
        setUserIdError(true);
      } ////////// if ////////
      // 4-2.   
      else {
        // (1)   
        setUserIdError(false);
        // (2)   :  == 
        if (pwd === result.pwd) {
          //    
        //   alert("Login Success!");

          // ****** [    ] ****** //
          // 1.    !
          // ->    !
          // ->  result  
          // ->  JSON.stringify()
          sessionStorage.setItem("minfo", JSON.stringify(result));

          // 2.  API  
          myCon.setLoginSts(sessionStorage.getItem("minfo"));
          // ->  minfo  !

          // 3.    
          myCon.makeMsg(result.unm);

          // 4.     
          document.querySelector(".sbtn").innerText = "Login";

          // 5.   
          // 1   
          setTimeout(() => {
            myCon.goPage("/");
          }, 1000);
        } //// if /////
        //    !
        else {
          // (1)   
          setPwdMsg(msgPwd[1]);
          // (2)   
          setPwdError(true);
        } ////// else //////

        // ->     
        //      !
      } ////// else //////

      // .find() ->    
      // find filter    
      //  .   
      // undefined  !
    } ///////// if /////////
    // 3.  /////
    else {
      alert("Change your input!");
    } //// else ///////////
  }; /////////// onSubmit  //////////

  //   /////////
  useEffect(() => {
    //  
    document.querySelector("#user-id").focus();
  }, []);

  //   ////////////////////////
  return (
    <div className="outbx">
      <section className="membx" style={{ minHeight: "300px" }}>
        <h2>LOG IN</h2>
        <form method="post" action="process.php">
          <ul>
            <li>
              <label>ID : </label>
              <input
                id="user-id"
                type="text"
                maxLength="20"
                placeholder="Please enter your ID"
                value={userId}
                onChange={changeUserId}
              />
              {
                //    
                //  && 
                userIdError && (
                  <div className="msg">
                    <small
                      style={{
                        color: "red",
                        fontSize: "10px",
                      }}
                    >
                      {idMsg}
                    </small>
                  </div>
                )
              }
            </li>
            <li>
              <label>Password : </label>
              <input
                type="password"
                maxLength="20"
                placeholder="Please enter your Password"
                value={pwd}
                onChange={changePwd}
              />
              {
                //    
                //  && 
                pwdError && (
                  <div className="msg">
                    <small
                      style={{
                        color: "red",
                        fontSize: "10px",
                      }}
                    >
                      {pwdMsg}
                    </small>
                  </div>
                )
              }
            </li>
            <li style={{ overflow: "hidden" }}>
              <button className="sbtn" onClick={onSubmit}>
                Submit
              </button>
            </li>
          </ul>
        </form>
      </section>
    </div>
  );
}

export default Login;
